:root {
  --tabs-color: var(--color-body);
  --tabs-background: var(--color-shadow);
  --tabs-active-color: var(--color-black);
  --tabs-active-background: var(--color-white);
  
}

.Tabs {
  height: 100%;
  margin-top: var(--spacing-xxlg);
  position: relative;

}

.Tabs__list {
  display: flex;
  list-style-type: none;
  margin-left: var(--spacing-md);
  margin-right: var(--spacing-md);
  @media (--medium-viewport) { 
    margin-left: calc(26% + var(--spacing-xxlg));
    margin-right: var(--spacing-xxlg);
    padding-left: var(--spacing-md)
  }
 
  @media (--xlarge-viewport) { 
    max-width: var(--wrapper-width);
    
  }
  
}

.Tabs__panels {
  background-color: var(--color-white);
  padding-bottom: var(--spacing-vertical--3);
}

.Tabs__panel {
  flex: 1;
  outline: none;
}

.Tabs__link { 
  padding: calc(var(--spacing-vertical--1) / 2) var(--spacing-xxlg);
  text-decoration: none;
  border: 0;
  color: var(--tabs-color);
  background-color: var(--tabs-background);
  display: block;
  cursor:pointer;
  margin-right: 0.25rem;
  line-height: var(--baseline);
  text-transform: capitalize;

  &[aria-selected="true"] {
    background-color: var(--tabs-active-background);
    color: var(--tabs-active-color);
  }

  &:focus {
    outline: 0;
  }
}
